"use client";

import { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  Handshake,
  Gauge,
  Bell,
  List,
  LineChart,
  Settings2,
  RotateCcw,
  X,
  User2,
} from "lucide-react";

export function StatusBar() {
  // Palette fidèle aux captures
  const deepBlue = "#0b1235"; // topbar
  const grey = "#d5d7dd";     // statusbar
  const greyDark = "#c8cad1";
  const orange = "#ff6600";

  // hauteurs
  const headerHeight = 64;   // topbar bleu
  const statusHeight = 64;   // bande grise
  const menuTop = headerHeight + statusHeight;

  // Heure / date en live (format US comme sur la capture)
  const [now, setNow] = useState(new Date());
  useEffect(() => {
    const id = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(id);
  }, []);
  const timeStr = useMemo(() => {
    return now.toLocaleTimeString("en-US", { hour12: true });
  }, [now]);
  const dateStr = useMemo(() => {
    return now.toLocaleDateString("en-US");
  }, [now]);

  const [open, setOpen] = useState(false);

  const menuItems = [
    { href: "/control",   icon: <Handshake size={28} />, label: "CONTROL" },
    { href: "/dashboard", icon: <Gauge size={28} />,     label: "DASHBOARD" },
    { href: "/events",    icon: <Bell size={28} />,      label: "EVENTS" },
    { href: "/data",      icon: <LineChart size={28} />, label: "DATA" },
    { href: "/recipe",    icon: <List size={28} />,      label: "RECIPE" },
    { href: "/settings",  icon: <Settings2 size={28} />, label: "SETTINGS" },
  ];

  return (
    <div style={{ position: "relative", zIndex: 0 }}>
      {/* ---------------- TOPBAR (bleu nuit) ---------------- */}
      <div
        style={{
          height: headerHeight,
          background: deepBlue,
          color: "white",
          display: "flex",
          alignItems: "center",
          paddingLeft: 16,
          paddingRight: 16,
          position: "relative",
        }}
      >
        {/* Logo + version */}
        <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
          {/* Remplace src par ton logo si tu l’as */}
          <img
            src="/logo-stirweld.svg"
            alt="stirweld"
            onError={(e: any) => {
              // fallback texte si l'image n'existe pas
              e.currentTarget.style.display = "none";
            }}
            style={{ height: 28 }}
          />
          <span style={{ fontWeight: 800, letterSpacing: 1, display: "inline-block" }}>
            {/* fallback texte si pas d’image */}
            <span style={{ display: "inline-block" }}>stirweld</span>
          </span>

          <span style={{ marginLeft: 8, opacity: 0.9, fontWeight: 800 }}>V4 v2.0</span>
        </div>

        {/* Burger (3 traits orange) */}
        <div
          onClick={() => setOpen((o) => !o)}
          title="Menu"
          style={{
            position: "absolute",
            left: 16,
            bottom: -statusHeight / 2 + 6, // visuellement comme sur la capture (débordant sur la barre grise)
            display: "flex",
            flexDirection: "column",
            gap: 8,
            cursor: "pointer",
          }}
        >
          {[0, 1, 2].map((i) => (
            <div
              key={i}
              style={{
                width: 210,
                height: 12,
                background: orange,
              }}
            />
          ))}
        </div>

        {/* Droite : heure / date / user */}
        <div style={{ marginLeft: "auto", display: "flex", alignItems: "center", gap: 24 }}>
          <div style={{ fontSize: 22, fontWeight: 800 }}>{timeStr}</div>
          <div style={{ fontSize: 24, fontWeight: 900 }}>{dateStr}</div>

          <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
            <div
              style={{
                width: 56,
                height: 56,
                borderRadius: "50%",
                background: "white",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                color: deepBlue,
                fontWeight: 800,
              }}
            >
              <User2 />
            </div>
            <div style={{ fontWeight: 700, fontSize: 14, marginTop: 2 }}>_SystemUser</div>
          </div>
        </div>
      </div>

      {/* ---------------- STATUS BAR (grise) ---------------- */}
      <div
        style={{
          height: statusHeight,
          background: grey,
          color: "#0b1235",
          position: "relative",
          display: "flex",
          alignItems: "center",
          borderTop: `1px solid ${greyDark}`,
        }}
      >
        {/* triangle oblique à gauche */}
        <div
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: 0,
            height: 0,
            borderTop: `${statusHeight}px solid ${grey}`,
            borderRight: `${statusHeight}px solid transparent`,
          }}
        />

        {/* libellés "Status" + titre */}
        <div style={{ paddingLeft: statusHeight + 56, display: "flex", alignItems: "center" }}>
          <div style={{ padding: "0 24px", fontSize: 18, fontWeight: 700 }}>Status</div>
          <div style={{ padding: "0 24px", fontSize: 36, fontWeight: 800 }}>Textblock</div>
        </div>

        {/* champs valeurs */}
        {[
          ["F", "kN"],
          ["TT", "°C"],
          ["RS", "RPM"],
          ["ZP", "mm"],
        ].map(([label, unit], idx) => (
          <div
            key={idx}
            style={{
              display: "flex",
              alignItems: "center",
              gap: 10,
              padding: "0 24px",
              borderLeft: `1px solid ${greyDark}`,
              marginLeft: idx === 0 ? 16 : 0,
            }}
          >
            <span style={{ fontSize: 14, fontWeight: 700 }}>{label}</span>
            <div
              style={{
                width: 100,
                height: 36,
                background: "#f5f6f8",
                border: `1px solid ${greyDark}`,
                borderRadius: 2,
              }}
            />
            <span style={{ fontSize: 14, fontWeight: 700 }}>{unit}</span>
          </div>
        ))}

        {/* bouton rond refresh à droite */}
        <div style={{ marginLeft: "auto", paddingRight: 24 }}>
          <button
            aria-label="Refresh"
            style={{
              width: 56,
              height: 56,
              borderRadius: "50%",
              background: deepBlue,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              border: "none",
              cursor: "pointer",
            }}
          >
            <RotateCcw style={{ color: "white" }} />
          </button>
        </div>
      </div>

      {/* ---------------- OVERLAY pour fermer le menu ---------------- */}
      {open && (
        <div
          onClick={() => setOpen(false)}
          style={{
            position: "fixed",
            top: menuTop,
            left: 0,
            right: 0,
            bottom: 0,
            background: "transparent",
            zIndex: 900,
          }}
        />
      )}

      {/* ---------------- MENU LATÉRAL (style 2ᵉ capture) ---------------- */}
      {open && (
        <aside
          style={{
            position: "fixed",
            top: menuTop,
            left: 16,
            bottom: 0,
            width: 240,
            background: deepBlue,
            paddingTop: 16,
            paddingBottom: 16,
            zIndex: 1000,
            display: "flex",
            flexDirection: "column",
            gap: 16,
          }}
        >
          {/* bouton X */}
          <div style={{ display: "flex", justifyContent: "flex-end", paddingRight: 12 }}>
            <X color="#fff" style={{ cursor: "pointer" }} onClick={() => setOpen(false)} />
          </div>

          {/* items larges avec icône blanche + label (comme la 2ᵉ image) */}
          <div style={{ display: "flex", flexDirection: "column", gap: 24, padding: "0 12px" }}>
            {menuItems.map((item) => (
              <Link
                key={item.href}
                href={item.href}
                onClick={() => setOpen(false)}
                style={{
                  display: "flex",
                  alignItems: "center",
                  gap: 16,
                  color: "#ffffff",
                  textDecoration: "none",
                  fontWeight: 800,
                  letterSpacing: 1,
                }}
              >
                <div
                  style={{
                    width: 84,
                    height: 84,
                    borderRadius: 12,
                    background: "transparent",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    border: "2px solid transparent",
                  }}
                >
                  {item.icon}
                </div>
                <span style={{ fontSize: 28 }}>{item.label}</span>
              </Link>
            ))}
          </div>
        </aside>
      )}
    </div>
  );
}
